import * as d3 from 'd3';
import Color from '../../visualization/color';
import Annotator from './annotator'

class Line extends Annotator {
    annotate(chart, target, style) {
        let svg = chart.svg();
        d3.selection.prototype.moveToFront = function() {  
            return this.each(function(){
                this.parentNode.appendChild(this);
            });
        };
        let tooltip_id = `${chart.container()}-tooltip`;
        let Tooltip = d3.select(tooltip_id);
        let pr = target['pr'];
        let pvalue = target['pvalue']
        let relationship_text = ""
        if (pr <= -0.7) {
            relationship_text = "a strong negative linear relationship";
        } else if (pr >= 0.7) {
            relationship_text = "a strong positive linear relationship";
        } else if (pr > -0.7 && pr <= -0.3) {
            relationship_text = "a moderate negative linear relationship";
        } else if (pr < 0.7 && pr >= 0.3) {
            relationship_text = "a moderate positive linear relationship";
        } else {
            relationship_text = "a weak relationship"
        }
        Tooltip
            .html(`The data points in this category has ${relationship_text}. The probability of the correlation is about ${((1-pvalue)*100).toFixed(2)}%.`);
        var mouseover = function(d) {
            Tooltip
            .html(`The data points in this category has ${relationship_text}. The probability of the correlation is about ${((1-pvalue)*100).toFixed(2)}%.`)
            .style("opacity", 1)
            d3.select(this)
            .style("stroke", "black")
        }
        var mouseout = function(d) {
            Tooltip
            .html("<p>You could hover over an annotation to see the caption.<p/>")
            .style("opacity", 1)
            d3.select(this)
            .style("stroke",  function(d) {
                if ('color' in style) {
                    return style['color']
                } else {
                    return Color().ANNOTATION
                }
            })
        }
        let x1 = target['x1'], y1 = target['y1'], x2 = target['x2'], y2 = target['y2'];
        if (x1 !== undefined && y1 !== undefined && x2 !== undefined && y2 !== undefined) {
            svg.append("line")
                .attr("class", "line")
                .attr("x1", chart.xScale()(x1))
                .attr("x2", chart.xScale()(x2))
                .attr("y1", chart.yScale()(y1))
                .attr("y2", chart.yScale()(y2))
                .attr("stroke", function(d) {
                    if ('color' in style) {
                        return style['color']
                    } else {
                        return Color().ANNOTATION
                    }
                })
                .attr("stroke-width", 3)
                .on("mouseover", mouseover)
                .on("mouseout", mouseout);

        } else {
            console.log('incorrect spec');
        };
    }
}

export default Line