import * as d3 from 'd3';
import Color from '../../visualization/color';
import Annotator from './annotator';
import numeric from 'numeric';
import jStat from 'jstat';

// Inspired by https://plnkr.co/edit/iLNvpm?preview
// http://www.cs.utah.edu/~tch/CS4300/resources/refs/ErrorEllipses.pdf

class Cluster extends Annotator {
    annotate(chart, target, style) {
        let svg = chart.svg();
        let focus_elements = svg.selectAll(".mark")
            .filter(function (d) {
                if (target.length === 0) {
                    return true
                }
                for (const item of target) {
                    if (d[item.field] === item.value) {
                        return true
                    }
                }
                return false
            });
        if (focus_elements.empty()) return;
        let clustered_items = target[0]['items'];
        let colors = new Set(clustered_items.map(x => x['color']))
        let text;
        if (colors.size > 1) {
          text = `These data points in the circle are annotated as a cluster due to the density. They belong to ${colors.size} different categories.`;
        } else {
          text = `These data points in the circle are annotated as a cluster due to the density. They all belong to 1 category.`;
        }
        let tooltip_id = `${chart.container()}-tooltip`;
        let Tooltip = d3.select(tooltip_id).html(text);
        var mouseover = function(d) {
            Tooltip
            .html(text)
            .style("opacity", 1)
            d3.select(this)
            .style("stroke", "black")
        }
        var mouseout = function(d) {
            Tooltip
            .html("<p>You could hover over an annotation to see the caption.<p/>")
            d3.select(this)
            .style("stroke",  function(d) {
                if ('color' in style) {
                    return style['color']
                } else {
                    return Color().ANNOTATION
                }
            })
        }

        // step 1: get all focused elements position
        let xEncoding = chart.x;
        let yEncoding = chart.y;
        let xs = [];
        let ys = [];
        for(let focus_element of focus_elements.nodes()) {
            xs.push(focus_element.__data__[xEncoding]);
            ys.push(focus_element.__data__[yEncoding]);
        }
        let p = 0.95;
        if ('p' in style) {
            p = style['p']
        }
        let computed_parameter = this.compute(xs, ys, p);
        let xExtent = d3.extent(xs);
        let yExtent = d3.extent(ys);
        
        let cx = computed_parameter['cx'], cy = computed_parameter['cy'], rx = computed_parameter['rx'], ry = computed_parameter['ry'];
        rx = Math.abs(chart.xScale()(xExtent[0] + computed_parameter['rx']) - chart.xScale()(xExtent[0]))
        ry = Math.abs(chart.yScale()(yExtent[0] + computed_parameter['ry']) - chart.yScale()(yExtent[0]))
        let rotation = computed_parameter['rotation'];
        d3.selection.prototype.moveToFront = function() {  
            return this.each(function(){
                this.parentNode.appendChild(this);
            });
        };
        if (cx !== undefined && cy !== undefined && rx !== undefined && ry !== undefined && rotation !== undefined) {
            svg.append("ellipse")
                .attr("fill", "none")
                .attr("cx", chart.xScale()(cx))
                .attr("cy", chart.yScale()(cy))
                .attr("rx", rx)
                .attr("ry", ry)
                .attr("stroke-width", 3)
                .attr("stroke", function(d) {
                    if ('color' in style) {
                        return style['color']
                    } else {
                        return Color().ANNOTATION
                    }
                })
                
                .attr("transform", "rotate(" + rotation + "," + chart.xScale()(cx) + "," + chart.yScale()(cy) + ")").on("mouseover", mouseover)
                .on("mouseout", mouseout);

        } else {
            console.log('incorrect spec');
        }
        
    }

    indexOfMax = function(data) {
        var max = data[0],
          index = 0;
        for (var d in data) {
          if (data[d] > max) {
            max = data[d];
            index = d;
          }
        }
        return index;
      };
      
    indexOfMin = function(data) {
        var min = data[0],
          index = 0;
        for (var d in data) {
          if (data[d] < min) {
            min = data[d];
            index = d;
          }
        }
        return index;
      };

    compute(xs, ys, p = 0.9) {
        let stdDevX = d3.deviation(xs);
        let stdDevY = d3.deviation(ys);
        let cor = jStat.corrcoeff(xs, ys);
        let center = {
            x: d3.mean(xs),
            y: d3.mean(ys)
        };
        let level = p
        let xExtent = d3.extent(xs);
        let yExtent = d3.extent(ys);
        let ar = (d3.mean(ys) / (yExtent[1] - yExtent[0]))/(d3.mean(xs) / (xExtent[1] - xExtent[0]));
        return this.errorEllipse(stdDevX, stdDevY, cor, center, level, ar)
    }

    errorEllipse = function(stdDevX, stdDevY, cor, center, level, aspectRatio) {
        var errEllipse,
          cov = cor * stdDevX * stdDevY,
          covmat = [
            [stdDevX * stdDevX, cov],
            [cov, stdDevY * stdDevY]
          ],
          eig = numeric.eig(covmat),
          scale = Math.sqrt(jStat.chisquare.inv(level, 2)),
          maxLambdaI = this.indexOfMax(eig.lambda.x),
          minLambdaI = this.indexOfMin(eig.lambda.x),
          rx = stdDevX > stdDevY ? Math.sqrt(eig.lambda.x[maxLambdaI]) * scale : Math.sqrt(eig.lambda.x[minLambdaI]) * scale,
          ry = stdDevY > stdDevX ? Math.sqrt(eig.lambda.x[maxLambdaI]) * scale : Math.sqrt(eig.lambda.x[minLambdaI]) * scale,
          theta =  0.5 * Math.atan((1 / aspectRatio) * ((2 * cov) / (Math.pow(stdDevX,2) - Math.pow(stdDevY, 2))));
        
        // console.log(theta * 180 / Math.PI)

        if (theta < 0) {
            theta += 2 * Math.PI
        }
        
        //make the ellipse object
        errEllipse = {
          'rx': rx,
          'ry': ry,
          'cx': center.x,
          'cy': center.y,
          'rotation': - (theta * 180 / Math.PI)
        };
        return errEllipse;
      };

}

export default Cluster