import * as d3 from 'd3';
import Color from '../../visualization/color';
import Annotator from './annotator'

class Point extends Annotator {
    annotate(chart, target, style) {
        let svg = chart.svg();
        d3.selection.prototype.moveToFront = function() {  
            return this.each(function(){
                this.parentNode.appendChild(this);
            });
        };
        let x = target['x'], y = target['y'];
        let outlier_probability = target['outlier_probability'];
        const circleSize = 8;
        let xScale = chart.xScale();
        let yScale = chart.yScale();
        if (x !== undefined && y !== undefined) {
            let tooltip_id = `${chart.container()}-tooltip`;
            let Tooltip = d3.select(tooltip_id).html(`This item (${x},${y}) is annotated as an outlier. <br/>The outlier probability is ${(outlier_probability*100).toFixed(2)}%.`);
            var mouseover = function(d) {
                Tooltip
                .html(`This item (${x},${y}) is annotated as an outlier. <br/>The outlier probability is ${(outlier_probability*100).toFixed(2)}%.`)
                .style("opacity", 1)
                d3.select(this)
                .style("stroke", "black")
            }
            var mouseout = function(d) {
                Tooltip
                .html("<p>You could hover over an annotation to see the caption.<p/>")
                .style("opacity", 1)
                d3.select(this)
                .style("stroke",  function(d) {
                    if ('color' in style) {
                        return style['color']
                    } else {
                        return Color().ANNOTATION
                    }
                })
            }
            svg.append("circle")
                .attr("class", "point")
                .attr("r", circleSize)
                .attr("stroke",function(d) {
                    if ('color' in style) {
                        return style['color']
                    } else {
                        return Color().ANNOTATION
                    }
                })
                .attr("stroke-width", 3)
                .attr("fill","transparent")
                .attr("opacity", 1)
                .attr("cx", xScale(x))
                .attr("cy", yScale(y))
                .on("mouseover", mouseover)
                .on("mouseout", mouseout);;

            // TODO: Label items if pointing a ata item

        } else {
            console.log('incorrect spec');
        }
        
    }
}

export default Point