import * as d3 from 'd3';
import Color from '../../visualization/color';
import Annotator from './annotator'

class Ellipse extends Annotator {
    annotate(chart, target, style) {
        let svg = chart.svg();
        d3.selection.prototype.moveToFront = function() {  
            return this.each(function(){
                this.parentNode.appendChild(this);
            });
        };
        let cx = target['cx'], cy = target['cy'], rx = target['rx'], ry = target['ry'];
        let angle = 0;
        if ('angle' in target) { angle = target['angle']; }
        if (cx !== undefined && cy !== undefined && rx !== undefined && ry !== undefined) {
            console.log('append')
            svg.append("ellipse")
                .attr("fill", "none")
                .attr("cx", chart.xScale()(cx))
                .attr("cy", chart.yScale()(cy))
                .attr("rx", chart.rxScale()(rx))
                .attr("ry", chart.ryScale()(ry))
                .attr("stroke-width", 5)
                .attr("stroke", function(d) {
                    if ('color' in style) {
                        return style['color']
                    } else {
                        return Color().ANNOTATION
                    }
                })
                .attr("transform", "rotate(" + angle * (180/Math.PI) + "," + chart.xScale()(cx) + "," + chart.yScale()(cy) + ")");

            // TODO: Label items if pointing a ata item

        } else {
            console.log('incorrect spec');
        }
        
    }
}

export default Ellipse