import React from 'react';
import Chart from './Chart';
import './gallery.css';

const num_of_plots = 100

export default class Gallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {value: 1};
    this.handleChange = this.handleChange.bind(this);
    this.clickPrev = this.clickPrev.bind(this);
    this.clickNext = this.clickNext.bind(this);
    this.random = this.random.bind(this);
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  clickPrev() {
    let { value } = this.state;
    if (value > 1) {
        this.setState({value: parseInt(value) - 1});
    } else {
        this.setState({value: 100});
    }
  }

  clickNext() {
    let { value } = this.state;
    if (value < num_of_plots) {
        this.setState({value: parseInt(value) + 1});
    } else {
        this.setState({value: 1});
    }
  }

  random() {
    let randomNum = Math.floor(Math.random() * 100) + 1;
    this.setState({value: randomNum});
  }

  render() {
    const { value } = this.state;
    let origin_spec = require(`../benchmark/${value}/origin.json`);
    let flat_spec = require(`../benchmark/${value}/flat.json`);
    // let baseline_spec = require(`../benchmark/${value}/baseline.json`);
    // let origin_spec = require('../spec/test.json');
    let originview = <Chart spec={origin_spec} container="origin" showPanel={false}/>;
    // let baselineview = <Chart spec={baseline_spec} container="baseline" showPanel={false}/>;
    let flatview = <Chart spec={flat_spec} container="flat" showPanel={true}/>;
    let plot_options = [];
    for (let index = 1; index <= num_of_plots; index++) {
        plot_options.push(<option value={index}>{index}</option>)
    }
    return (
      <div style={{ height: "100%" }}>
        <div style={{paddingLeft:65, paddingTop: 10, paddingBottom: 30, backgroundColor: "#E8F0F8"}}>
          <h2>Annotating Scatterplots for Insight Communication</h2>
          <p style={{width: 530, fontSize: 16, textAlign: "justify"}}>Scatterplots here are randomly generated with three colored categories. An RL agent automatically annotates them to communicate insights, including outliers, clusters, and correlations. The purpose of the agent is to help people who lack visualization literacy understand the chart at a glance.</p>
          <div style={{fontSize: 20, fontWeight: "normal"}}>Plot: 
            <select style={{marginLeft:10, width:100, fontSize: 18}} value={this.state.value} onChange={this.handleChange}>
              {plot_options}
            </select>
            {/* <button style={{marginLeft:10, width:60, fontSize: 14}} onClick={this.clickPrev}>Prev</button>
            <button style={{marginLeft:2,  width:60, fontSize: 14}} onClick={this.clickNext}>Next</button> */}
            <button style={{marginLeft:20,  width:80, fontSize: 14}} onClick={this.random}>Random</button>
          </div>
        </div>
        <div>
            {/* <div style={{width:400, height:"auto", float:"left", display:"inline"}}>
                <p style={{paddingLeft:80, fontSize: 20}}>Original Plot</p>
                {originview}
            </div>  */}
            {/* <div style={{width:400, height:"auto", float:"left", display:"inline"}}>
                <p style={{paddingLeft:80, fontSize: 20}}>Baseline Plot</p>
                {baselineview}
            </div>  */}
            <div style={{width:500, height:"auto", float:"left", display:"inline"}}>
                {/* <p style={{paddingLeft:80, fontSize: 20}}>Annotated Plot (Episode Reward:)</p> */}
                {flatview}
            </div>
        </div>
      </div >
    )
  }
}

