import * as d3 from 'd3';

class Chart {
    constructor() {
        this._container = document.createElement("div");
        this._svg = d3.select(this._container).append("svg")
        this._xScale = {};
        this._yScale = {};
        this._rxScale = {};
        this._ryScale = {};
        this._width = 0;
        this._height = 0;
        this._data = [];
        this._processedData = [];

        // this._factdata = [];
        // this._subspace = [];
        // this._measure = [];
        // this._breakdown = [];
        // this._focus = [];
    }

    container(value) {
        if (!value) {
            return this._container;
        }
        this._container = value;
    }

    clear() {
        d3.select(this._container).selectAll("*").remove();
    }

    svg() {
        return this._svg;
    }

    xScale(value) {
        if (!value) {
            return this._xScale;
        }
        this._xScale = value;
    }

    yScale(value) {
        if (!value) {
            return this._yScale;
        }
        this._yScale = value;
    }

    width(value) {
        if (!value) {
            return 320;
        }
        this._width = value;
    }

    height(value) {
        if (!value) {
            return 320;
        }
        this._height = value;
    }

    data(value) {
        if (!value) {
            return this._data;
        }
        this._data = value;
    }

    factdata(value) {
        if (!value) {
            return this._factdata;
        }
        this._factdata = value;
    }

    processedData(value) {
        if (!value && !this._processedData) {
            return this._data;
        }
        if (!value) {
            return this._processedData;
        }
        this._processedData = value;
    }

    subspace(value) {
        if (!value) {
            return this._subspace;
        }
        this._subspace = value;
    }

    measure(value) {
        if (!value) {
            return this._measure;
        }
        this._measure = value;
    }

    breakdown(value) {
        if (!value) {
            return this._breakdown;
        }
        this._breakdown = value;
    }

    focus(value) {
        if (!value) {
            return this._focus;
        }
        this._focus = value
    }

    visualize() { }

    drawAxis() { }

    encodeXY() { }

    encodeColor() { }

    encodeSize() { }

    encodeShape() { }

    addEncoding(channel, field) { }

    modifyEncoding(channel, field) { }

    removeEncoding(channel, field) { }

}

export default Chart;