import React, { Component } from 'react';
import { NarrativeChart } from '../vis';

export default class Chart extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showOutlier: true,
            showCluster: true,
            showCorrelation: true
        }
    }

    componentDidMount() {
        let spec = this.props.spec;
        this.vis = new NarrativeChart();
        let container = this.props.container;
        this.vis.container(`#${container}`);
        this.renderChart(spec)
    }

    componentDidUpdate() {
        let spec = this.props.spec;
        this.renderChart(spec)
    }

    outlier_checkbox = (e) => {
        this.setState({
            showOutlier: e.target.checked
        })
        this.renderChart(this.props.spec);
    }

    cluster_checkbox = (e) => {
        this.setState({
            showCluster: e.target.checked
        })
        this.renderChart(this.props.spec);
    }

    correlation_checkbox = (e) => {
        this.setState({
            showCorrelation: e.target.checked
        })
        this.renderChart(this.props.spec);
    }

    replay = () => {
        this.renderChart(this.props.spec);
    }

    renderChart(spec) {
        let newSpec = JSON.parse(JSON.stringify(spec));
        let annotations = spec['actions'].slice(5);
        newSpec['actions'] = spec['actions'].slice(0,5);
        for (let index = 0; index < annotations.length; index++) {
            const annotation = annotations[index];
            if ((annotation["method"] === "point" && this.state.showOutlier) || (annotation["method"] === "line" && this.state.showCorrelation) || (annotation["method"] === "cluster" && this.state.showCluster)) {
                newSpec['actions'].push(annotation)
            }
        }
        this.vis.stop();
        this.vis.load(newSpec);
        this.vis.generate();
    }

    render() {
        let chartwidth = 400;
        let panelwidth = 150;
        let height = 400, width = chartwidth + panelwidth;
        let actions = this.props.spec['actions']
        let opacity = actions.length > 5 ? 1 : 0;
        let line_count = 0, point_count = 0, cluster_count = 0;
        for (const action of actions) {
            switch (action["method"]) {
                case "point":
                    point_count += 1
                    break;
                case "line":
                    line_count += 1
                    break;
                case "cluster":
                    cluster_count += 1
                    break;
                default:
                    break;
            }
        }
        let summary_text = "To summarize the scatterplot, we ";
        if (point_count > 0) {
            summary_text += "highlight " + point_count + " outlier point"
            if (point_count > 1) {summary_text += "s"}
        }
        if (line_count > 0) {
            if (summary_text[summary_text.length - 1] !== " ") {
                summary_text += ", "
            }
            summary_text += "draw " + line_count + " correlation line"
            if (line_count > 1) {summary_text += "s"}
        }
        if (cluster_count > 0) {
            if (summary_text[summary_text.length - 1] !== " ") {
                summary_text += ", "
            }
            summary_text += "circle " + cluster_count + " data cluster"
            if (cluster_count > 1) {summary_text += "s"}
        }
        summary_text += "."
        let panel = null;
        if (this.props.showPanel) {
            panel = <div style={{width:panelwidth, height:"auto", float:"left", display:"inline"}}>
                <p>Annotation Legend</p>
                <p>
                    {/* <input onChange={this.outlier_checkbox} name={1} type="checkbox" id="scales1" checked={this.state.showOutlier}></input>  */}
                    < img alt="" src={require('../img/outlier.png')} width={25} style={{verticalAlign: "middle"}}/> outlier </p >
                <p>
                    {/* <input onChange={this.correlation_checkbox} name={3} type="checkbox" id="scales3" checked={this.state.showCorrelation}></input>  */}
                    < img alt="" src={require('../img/correlation.png')} width={25} style={{verticalAlign: "middle"}}/> correlation</p >
                <p>
                    {/* <input onChange={this.cluster_checkbox} name={2} type="checkbox" id="scales2" checked={this.state.showCluster}></input>  */}
                    < img alt="" src={require('../img/cluster.png')} width={25} style={{verticalAlign: "middle"}}/> cluster </p >
                {/* <br/>
                <p>{summary_text}</p> */}
                <button style={{marginLeft:2, marginTop:20,  width:60, fontSize: 14}} onClick={this.replay}>Play</button>
            </div>
        }
        return (
            <div style={{ marginLeft: 30, marginTop: 30, height: height + 100, width: width}}>

                <div style={{width:chartwidth, height:"auto", float:"left", display:"inline"}}>
                    <div id={this.props.container}
                        style={{
                            height: height,
                            width: 420,
                            display: "flex",
                            flexDirection: "column",
                            alignItems: "center",
                            justifyContent: "center"
                        }} />
                    
                    <div id={this.props.container+'-tooltip'}
                        style={{
                            opacity: opacity,
                            backgroundColor: 'white',
                            width: chartwidth-90,
                            // height: 60,
                            border: "solid",
                            borderWidth: 2,
                            marginLeft: 50,
                            padding: 5
                        }}>
                        {/* {summary_text} */}
                        You could hover over an annotation to see the caption.
                    </div>
                </div> 
                {panel}
                {/* <div style={{width:panelwidth, height:"auto", float:"left", display:"inline"}}>
                    <p>panels</p>
                </div> */}

            </div>
        )
    }
}
